<template>
  <div id="topBarDiv">
    <button id="mesNotButton" class="topBarButton" @click="mesNotClick">{{ mesNotButton}}</button>
  </div>
</template>

<script>
  export default {
    name: 'topBarDiv',
    data () {
      return {
        mesNotButton: '个人信息',
        mesNotUrl: 'http://localhost:8080/',
      }
    },
    methods: {
      mesNotClick: function () {
        if(window.location.href == this.mesNotUrl) {
          var mesNotButton = document.getElementById('mesNotButton');
          mesNotButton.style.backgroundColor = "#00a539";
        }
      }}
  }
</script>

<style lang="css">
  html{
    font-size: 16px;
  }
  #topBarDiv{
    /*text-align: left;*/
    background-color: #158064;
    width: 100%;
    height: 4rem;
    position: relative;
    overflow: hidden;
    display: flex;

  }
 /*li{
    list-style: none;
    float: left;
    margin-right: 12rem;
  }*/

   .topBarButton,.topBarButton:active{
    display: block;
    font-weight:normal ;
    color: white;
    background-color: #00a539;
    text-align: center;
    font-size: 1rem;
    padding: 1rem 3rem;
    border: none;
    outline: none;
     position:relative;
     left:9rem;
  }
   .topBarButton:hover {
    background-color:#00a539;
    cursor: pointer;
  }
  /*@media screen and (max-width:1023px) {
    html {
      font-size: 9px;
    }
  }*/
</style>



