<template>
    <div id="checkCourseDiv">
        <div id="operationDiv">
            <div id="selectDiv">
                <select id="termSelect" v-model="termSelect">
                    <option disabled>请选择学期</option>
                </select>
                <select id="weekSelect" v-model="weekSelect">
                    <option disabled>请选择周数</option>
                </select>
                <button id="queryButton" @click="queryCourseClick()"  class="am-btn am-btn-success am-radius">查找</button>
            </div>
        </div>

        <div id="mainDiv">
            <p id="tableTipP">支持分别以学期和周数为条件，对全校的课程表进行查询；支持调换选定的两门课程。</p>
            <p id="tableInfoP">当前课表：{{ term }} {{ week }} </p>
            <tableDiv :queryCourse="queryCourse"></tableDiv><!--表格组件-->
        </div>
    </div>
</template>
<!--待完善查询课表的数据交互，需要确认后端的查询方式；待完善下拉搜索功能，需要确认后端提供数据库搜索支持还是前端通过js搜索处理-->
<script>
    import tableDiv from '../tableDiv/tableDiv.vue'
    export default {
        name: 'checkCourseDiv',
        data () {
            return {
                term: '',//预想显示搜索后的课表信息标题
                week: '',
                termSelect: '请选择学期',
                weekSelect: '请选择周数',
                queryCourse: []
            }
        },
        components: {
            tableDiv
        },
        methods: {
            queryCourseClick: function(){
                this.$http.post('./acdeminSeeCurriculum',{
//                this.$http.post('../testPhp/checkCourseQuery.php',{
                    "year": "2016-2017",
                    "term": "1",
                    "week": "1"
                },{
                    "Content-Type":"application/json"
                }).then(function(response){
                    console.log("查找课表:");
                    console.log(response.body);
                    this.queryCourse = response.body.course;
                },function(error){
                    console.log("查找课表error:");
                    console.log(error);
                });
            }
        }
    }
</script>

<style scoped>
    #checkCourseDiv{
        background-color: #f3f3f3;
    }
    #mainDiv{
        /*页面*/
        background-color: white;
        margin: 0 5rem;
        min-height: 33rem;
    }
    select{
        margin-right: 1.4rem;
    }
    #operationDiv{
        background-color: white;
        margin: 0 0 0.6rem;
    }
    #selectDiv{
        padding: 0.6rem 5rem;
    }
    #tableTipP{
        /*功能页面标题*/
        color: #C1C1C1;
        position: relative;
        text-align: left;
        z-index: 2;
        padding: 1rem;
        margin: 0;
    }
    #tableInfoP{
        /*表格课表标题*/
        text-align: left;
        position: relative;
        margin-top: 0;
        left: 1rem;
    }
    @media screen and (max-width:1023px) {
        html {
        }
    }
</style>



